.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.box {
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
}
.item {
  width: 400px;
  background-color: #f0f0f0;
  margin: 10px;
  border-radius: 10px;
  padding: 10px;
  padding-top: 0;
}

.item img {
  width: 100px;
  height: 100px;
}
